.loading-screen,
.login-screen {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

/* loading screen should be black like Windows boot */
.loading-screen {
  background-color: #000;
  flex-direction: column;
}

/* windows logo above spinner */
.windows-logo svg {
  width: 80px;
  height: 80px;
  fill: #fff;
  margin-bottom: 40px;
}

/* four-dot spinner mimicking Windows animation */
.spinner {
  position: relative;
  width: 60px;
  height: 60px;
}
.spinner div {
  position: absolute;
  width: 12px;
  height: 12px;
  background: #fff;
  border-radius: 50%;
  opacity: 0.3;
  animation: spin-dot 1s linear infinite;
}
.spinner div:nth-child(1) { top: 0; left: 50%; transform: translate(-50%, -50%); animation-delay: 0s; }
.spinner div:nth-child(2) { top: 50%; right: 0; transform: translate(50%, -50%); animation-delay: 0.25s; }
.spinner div:nth-child(3) { bottom: 0; left: 50%; transform: translate(-50%, 50%); animation-delay: 0.5s; }
.spinner div:nth-child(4) { top: 50%; left: 0; transform: translate(-50%, -50%); animation-delay: 0.75s; }

@keyframes spin-dot {
  0% { opacity: 0.3; transform: scale(1); }
  50% { opacity: 1; transform: scale(1.3); }
  100% { opacity: 0.3; transform: scale(1); }
}

.login-screen {
  /* background set inline via React import */
  background-size: cover;
  background-position: center;
  cursor: pointer; /* indicate you can click */
}

.login-overlay {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
}

.login-info {
  color: white;
  text-align: center;
}

.login-info .time {
  font-size: 4rem;
  line-height: 1;
}

.login-info .date {
  font-size: 1.5rem;
  margin-top: 0.5rem;
}

/* simple home screen styling */
.home-screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-size: cover;
  background-position: center;
  color: #fff;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  user-select: none;
}

/* context menu styles */
.context-menu {
  position: absolute;
  list-style: none;
  margin: 0;
  padding: 4px 0;
  background: #fff;
  color: #000;
  border: 1px solid #ccc;
  box-shadow: 2px 2px 6px rgba(0,0,0,0.2);
  z-index: 1000;
}
.context-menu li {
  padding: 4px 20px;
  cursor: default;
}
.context-menu li:hover {
  background: #0060df;
  color: #fff;
}

.home-screen h1 {
  font-size: 3rem;
}
.home-screen p {
  margin-top: 1rem;
}

/* basic taskbar styling */
.taskbar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 40px;
  background: rgba(50, 50, 50, 0.98);
  display: flex;
  align-items: center;
  padding: 0 10px;
  color: #fff;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 0.9rem;
}
.taskbar-section {
  margin-right: 20px;
  display: flex;
  align-items: center;
}
.taskbar-section.clock {
  margin-left: auto;
}

/* start logo styling with four gradient blocks separated by transparency */
.start-logo {
  width: 24px;
  height: 24px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 2px; /* transparent gutters */
  background: transparent;
}
.start-piece {
  border-radius: 2px;
}
.start-piece:nth-child(1) { background: linear-gradient(135deg, #0060df 0%, #0050c8 100%); }
.start-piece:nth-child(2) { background: linear-gradient(45deg, #0060df 0%, #0050c8 100%); }
.start-piece:nth-child(3) { background: linear-gradient(225deg, #0060df 0%, #0050c8 100%); }
.start-piece:nth-child(4) { background: linear-gradient(315deg, #0060df 0%, #0050c8 100%); }







